export default {
    name:'selectService',
    data(){
        return{
            value:''
        }
    },
    
    methods:{
        loadMore() {
        this.loading = true;
        setTimeout(() => {
            let last = this.list[this.list.length - 1];
            for (let i = 1; i <= 10; i++) {
            this.list.push(last + i);
            }
            this.loading = false;
        }, 2500);
        }
    }
}



